import React from "react";
import { Container } from "react-bootstrap";
import { projects } from "../proyects";

import styled from "styled-components";

const Img = styled.img`
	width: 150px;
	height: 150px;
	cursor: pointer;
	border-radius: 20px 0 20px 0;
	border: 1px solid white;

	&:hover {
		opacity: 0.4;
	}
`;

const LinkStyled = styled.a`
	width: 250px;
	padding: 0;
`;

export default function Proyect() {
	return (
		<Container
			className="d-flex justify-content-start flex-wrap w-100"
			id="proyect-scroll"
			fluid
		>
			<div className="d-flex justify-content-center flex-wrap w-100">

			
			{projects.map((project, i) => (
				<div className="	" key={i}>
					{/* <h3 className="text-white w-25">{project.title}</h3> */}
					<LinkStyled
						href={project.url}
						target="_blank"
						rel="noopener noreferrer"
					>
						<Img className="m-4" src={project.img} alt={project.title} />
					</LinkStyled>
					<div className="text-white">
						<p className="mt-2">
							{/* <b className="border-bottom ">Descripcion: </b> */}
							{/* <span className="fw-bolder w-25 text-wrap">{project.description}</span> */}
						</p>
					</div>
				</div>
				
			))}
			</div>
		</Container>
	);
}
